import latestVersion from 'latest-version'
import inquirer from 'inquirer'
import { gt } from 'semver'
import { out } from '@elog/shared'
import { runCmdSync } from '../utils/run'
import { getPkgJSON } from '../utils/utils'
const { pkgJson } = getPkgJSON()

const enum PackageManager {
  NPM = 'npm',
  PNPM = 'pnpm',
  YARN = 'yarn',
}

const upgrade = async () => {
  const newVersion = await latestVersion(pkgJson.name)
  const currentVersion = pkgJson.version
  out.access('ÂΩìÂâçÁâàÊú¨', currentVersion)
  out.access('ÊúÄÊñ∞ÁâàÊú¨', newVersion)
  if (gt(newVersion, currentVersion)) {
    inquirer
      .prompt([
        {
          type: 'confirm',
          name: 'confirmed',
          message: 'ÊòØÂê¶Êõ¥Êñ∞',
          default: true,
        },
      ])
      .then((answers) => {
        const confirmed = answers.confirmed
        if (!confirmed) {
          out.access('ÂèñÊ∂àÊõ¥Êñ∞')
          process.exit(0)
        } else {
          inquirer
            .prompt([
              {
                type: 'list',
                name: 'cli',
                message: 'ËØ∑ÈÄâÊã©ÂåÖÁÆ°ÁêÜÂô®',
                default: 'npm',
                choices: [
                  {
                    name: PackageManager.NPM,
                    value: PackageManager.NPM,
                  },
                  {
                    name: PackageManager.PNPM,
                    value: PackageManager.PNPM,
                  },
                  {
                    name: PackageManager.YARN,
                    value: PackageManager.YARN,
                  },
                ],
              },
            ])
            .then((answers) => {
              const { cli } = answers
              if (cli === PackageManager.YARN) {
                runCmdSync(`yarn global add ${pkgJson.name}@${newVersion}`)
              } else {
                runCmdSync(`${cli} install ${pkgJson.name}@${newVersion} -g`)
              }
            })
        }
      })
  } else {
    out.access('ÊèêÁ§∫', `ÂΩìÂâçÂ∑≤ÊòØÊúÄÊñ∞ÁâàÊú¨: ${currentVersion}`)
    process.exit(0)
  }
}

export default upgrade
