import rimraf from 'rimraf'
import path from 'path'
import { out } from '@elog/shared'
import * as fs from 'fs'

const __cwd = process.cwd()

/**
 * Ê∏ÖÁêÜÊñáÁ´†
 */
export const cleanPost = (postPath: string) => {
  try {
    const dist = path.join(__cwd, postPath)
    rimraf.sync(dist)
    out.info('Ê∏ÖÁêÜÊñáÊ°£', dist)
  } catch (error: any) {
    out.err(`Ê∏ÖÁêÜÊñáÊ°£Â§±Ë¥•, ${error.message}`)
  }
}

/**
 * Ê∏ÖÁêÜÊñáÁ´†ÁºìÂ≠ò
 */
export const cleanCache = (cachePath: string) => {
  try {
    const dist = path.join(__cwd, cachePath)
    fs.unlinkSync(dist)
    out.info('Ê∏ÖÁêÜÁºìÂ≠ò', dist)
  } catch (error: any) {
    out.err(`Ê∏ÖÁêÜÁºìÂ≠òÂ§±Ë¥•', ${error.message}`)
  }
}

